	.card{width: 90%;}
}
@media (min-width:768px) and (max-width: 1021px){
	.card{width: 46%;}
}
@media (min-width:1021px) and (max-width: 1115px){
	.card{width: 46%;}
}
@media (min-width:1115px) and (max-width: 1450px){
	.card{width: 46%;}
}
@media (min-width:1451px) and (max-width: 1500px){
	.card{width: 46%;}
}

@media (min-width:1501px)and (max-width: 1690px){
	.card{width: 31%;}
