.card{
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	margin: auto;
	margin-top: 15px;
	margin-left: 15px;
	margin-right: 15px;
	color: rgba(0,0,0,0.65);
	font-size: 14px;
	font-variant: tabular-nums;
	line-height: 1.5;
	list-style: none;
	-webkit-font-feature-settings: 'tnum';
	font-feature-settings: 'tnum';
	position: relative;
	background: #fff;
	border-radius: 2px;
	-webkit-transition: all .3s;
	transition: all .3s;
	border: 1px solid #e8e8e8;

	&:hover{
		border-color: rgba(0,0,0,0.09);
		-webkit-box-shadow: 0 2px 8px rgba(0,0,0,0.09);
		box-shadow: 0 2px 8px rgba(0,0,0,0.09);
	}

	.header_panel{
		display: flex;
		align-items: center;
		padding: 14px;

		.user_info{
			display: flex;

			strong{
				display: block;
				font-size: 16px;
				padding: 0 0 0 6px;
			}

			span.date{
				display: block;
				color: #ccc;
				padding: 0 0 0 6px;
			}
		}
		

		.settings{
			margin-left: auto;
			width: 30px;
			height: 30px;
			border-radius: 3px;
			text-align: center;
			line-height: 36px;
			

			i{
				font-size: 28px;
				font-weight: bold;
				transition: all .2s ease;

				&:hover{
					color:  #1890ff;
				}
			}

		}
	}

	.img_box{
		text-align: center;
		height: 260px;
		line-height: 260px;
		overflow: hidden;
	
		img{
			max-width: 100%;
			min-height: 100%;
			object-fit: contain;
		}
	}

	.interact_box{
		display: flex;
		padding: 12px;
	}

	.current_reacts{
		display: flex;
		padding: 0 0 0 4px;
		
		span{
			margin: 0 2px 0 0;
		}

		span:last-child {
			margin: 0;
		}
		
		i{
			display: block;
			font-size: 20px;
		}
	}

	.buttons_box{
		margin-left: auto;

		.add_react{
			margin-left: auto;
			padding: 0 10px 0 0;
		}
	}	

	.replies{
		display: block;
		padding: 10px 0;
		text-align: center;
		line-height: 32px;
		-webkit-transition: all .3s;
		transition: all .3s;
		border-top: 1px solid #e8e8e8;
	}
}

